.c
{
    height: 580px;
    width: 300px;
    margin: 20px 50px 50px 0px ;
    border-radius: 4px;
    border: 5px solid white;
    background-color: white;
    box-shadow: 0px 10px 10px 2px grey;
}


img
{
    height: 280px;
    width: 280px;
    margin: 10px 0px 0px 10px;
    object-fit: cover;
}       

h4
{
    margin: 10px 10px 10px 10px;
    background-color: white;
}

.price
{
    color: rgb(0, 126, 0);
    background-color: white;

}

.desc
{
    margin: 10px;
    background-color: white;

}

button
{
    margin: 25px;
    height: 40px;
    width: 250px;
    border-radius: 10px;
    border: none;
    font-size: 16px;
    border: 2px solid rgb(255, 0, 0);
    background-color: rgb(255, 142, 142);
    cursor: pointer;
}

span
{
    color: blue;
    cursor: pointer;
}
